.projects-section {
  background-color: #000;
  color: #fff;
  font-family: 'Figtree', sans-serif;
  padding: 4rem 2rem;
}

.projects-title {
  font-size: 40px;
  font-weight: 600;
  text-shadow: 2px 2px 8px rgba(255, 255, 255, 0.1);
}

.projects-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
}

.project-card {
  background-color: #111;
  border: 1px solid #333;
  border-radius: 16px;
  width: 280px;
  overflow: hidden;
  box-shadow: 0 4px 16px rgba(255, 255, 255, 0.05);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
}

.project-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 10px 30px rgba(255, 255, 255, 0.15);
  border: 1px solid rgba(0, 100, 200, 0.2);
}

.project-img {
  width: 100%;
  height: 180px;
  object-fit: cover;
  border-bottom: 1px solid #222;
  transition: transform 0.3s ease;
}

.project-card:hover .project-img {
  transform: scale(1.03);
}

.project-content {
  padding: 1rem;
  text-align: center;
}

.project-title {
  font-size: 22px;
  font-weight: 500;
  margin-bottom: 0.5rem;
  color: #fff;
}

.project-text {
  font-size: 14px;
  color: #ccc;
  line-height: 1.5;
}

.project-card.hide .project-content {
  opacity: 0;
  max-height: 0;
  padding: 0;
  transition: all 0.6s ease-in-out;
}

.project-card.show .project-content {
  opacity: 1;
  max-height: 300px;
  padding: 1rem;
  transition: all 0.6s ease-in-out;
}
