.services {
  width: 100%;
  background-color: black;
  padding: 0 7%;
  font-family: 'Figtree', sans-serif;
}

.services .text-title {
  color: white;
  font-size: 40px;
  font-weight: 600;
  margin-bottom: 50px;
  text-shadow: 2px 2px 8px rgba(255, 255, 255, 0.1);
}

.services .card {
  position: relative;
  display: flex;
  flex-direction: column;
  border: 1px solid rgba(255, 255, 255, 0.05); /* sutil borde */
  border-radius: 16px;
  background-color: #121212;
  overflow: hidden;
  min-height: 500px;
  box-shadow: 0 0 8px rgba(255, 255, 255, 0.03), 0 8px 20px rgba(0, 0, 0, 0.6);
  transition: transform 0.3s ease, box-shadow 0.3s ease, border 0.3s ease;
}

.services .card:hover {
  transform: translateY(-6px);
  box-shadow: 0 10px 30px rgba(255, 255, 255, 0.15);
  border: 1px solid rgba(0, 100, 200, 0.2);
}

.card-cover {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  opacity: 1;
  transition: all 0.6s ease-in-out;
  pointer-events: none;
}

.services .card:hover .card-cover {
  transform: translateY(-100%);
  opacity: 0;
  box-shadow: 0 10px 25px rgba(0, 159, 227, 0.2);
}

.card-body {
  opacity: 0;
  max-height: 0;
  overflow: hidden;
  padding: 0;
  transition: all 0.6s ease-in-out;
  color: white;
}

.services .card:hover .card-body {
  opacity: 1;
  max-height: 500px;
  padding: 1.5rem;
}

.services .card .card-body .card-title {
  font-size: 26px;
  font-weight: 500;
  margin-bottom: 20px;
  color: #ffffff;
}

.services .card .card-body p {
  font-size: 16px;
  color: #ccc;
  line-height: 1.6;
}

.services .card img {
  height: 200px;
  width: 200px;
  object-fit: contain;
  filter: drop-shadow(0 0 6px rgba(255, 255, 255, 0.1));
  transition: transform 0.3s ease;
}

.services .card:hover img {
  transform: scale(1.05);
}

.bold {
  font-weight: 600;
}

.pb-3,
.py-3 {
  padding-bottom: 1rem !important;
}

.pt-5,
.py-5 {
  padding-top: 1rem !important;
}

.pb-5,
.py-5 {
  padding-bottom: 1rem !important;
